/* Proj styles */
html {
    background-color: black;
    overflow: hidden; }

body {
    background-color: black;
    color: $color_light_orange;
    font-family: 'Futura', monospace;
    text-shadow: 1px 1px 5px #000000;
    color: #FAFCFB;
    margin: 0;
    height: 100%;
    overflow: hidden;

    a {
        color: $color_dark_orange; } }

.divider {
    border: 1px solid #A4A6A5;
    margin-bottom: 20px; }

#preloader {
    position: fixed;
    top: 0;
    left: 0;
    background-color: black;
    z-index: 9998;
    overflow: hidden;
    display: block;
    width: 100%;
    height: 100%;

    .crystal {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -60px;
        margin-left: -40px;
        width: 80px;
        z-index: 9999;

        @include breakpoint(tablet) {
            right: 19px; } }

    .loading {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: 50px;
        margin-left: -35px;
        z-index: 9999; } }
