.character-text {
    border-top: 2px solid #A4A6A5;
    margin-top: 15px;
    font-size: 18px;
    padding-top: 10px;

    h2 {
        text-align: center;
        font-size: 28px;
        margin-bottom: 40px;
        color: $color_headings; }

    .character-text-content {
        padding-top: 20px;
        display: none;

        &#character-soft-skills {
            ul {
                list-style-type: circle;
                li {
                    min-height: auto;
                    text-align: left;
                    clear: both;
                    width: 100%; } } }




        &.visible {
            display: block;

            .list-languages, .list-tools {
                list-style: none;

                .list-languages-item {
                    min-height: 250px;
                    text-align: center;

                    .sprite {
                        margin: 0 auto;
                        text-align: center;
                        display: block; }

                    .list-item-title {
                        text-transform: uppercase;
                        font-size: 20px;
                        margin-bottom: 10px; } } } }

        .list-works {
            .list-works-item {
                padding: 20px 0px 15px 0px;
                margin-bottom: 15px;
                border-bottom: 1px solid #A4A6A5; }

            .top {
                margin-bottom: 10px;

                .title-link {
                    text-decoration: none;
                    color: $color_yellow;
                    float: left;
                    margin-bottom: 10px;

                    @include breakpoint(phablet) {
                        width: 100%;
                        margin-top: 20px; }

                    &:hover {
                        text-decoration: underline; }

                    .title {
                        font-size: 20px;
                        font-weight: bold;
                        letter-spacing: 2px;
                        margin-bottom: 0; } }

                .access-link {
                    float: left;
                    margin-left: 30px;
                    background-color: $color_dark_orange;
                    color: white;
                    padding: 5px;
                    text-decoration: none;

                    @include breakpoint(phablet) {
                        width: 100%;
                        margin: 0 auto; }

                    &:hover {
                        background-color: #ff6200; } } }

            .container-image {
                overflow: hidden;
                position: relative;

                &.columns {
                    padding: 0; }

                .image {
                    float: left;
                    margin-left: {}

                    img {
                        vertical-align: middle; } }

                .image::before {
                    position: absolute;
                    top: 0;
                    left: -75%;
                    z-index: 2;
                    display: block;
                    content: '';
                    width: 50%;
                    height: 100%;
                    background: -webkit-linear-gradient(left, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
                    background: linear-gradient(to right, rgba(255,255,255,0) 0%, rgba(255,255,255,.3) 100%);
                    -webkit-transform: skewX(-25deg);
                    transform: skewX(-25deg); }

                .image:hover::before {
                    -webkit-animation: shine .75s;
                    animation: shine .75s; }

                @-webkit-keyframes shine {
                    100% {
                        left: 125%; } }


                @keyframes shine {
                    100% {
                        left: 125%; } } }

            .container-right {
                padding-left: 20px; }

            .subtitle {
                font-size: 18px;
                font-weight: bold;
                margin-bottom: 0; }

            .text {
                font-size: 17px;
                margin-bottom: 20px; }

            .list {
                list-style: none;
                float: left;
                margin-bottom: 0;

                .list-item {
                    background-color: $color_black;
                    color: white;
                    padding: 10px;
                    float: left;
                    margin: 5px 5px;
                    font-size: 15px; } } } } }

