.langs {
    margin-bottom: 20px;
    width: 100px;
    margin-left: 89%;

    @include breakpoint('phablet') {
        margin: -20px auto 30px; }

    a {
        font-size: 20px;
        color: $color_light_orange;
        font-family: "SSF4 ABUKET";
        text-decoration: none;

        &:hover {
            color: $color_light_orange;
            -webkit-text-stroke: 2px $color_dark_orange; }

        &.active {
            color: $color_light_orange;
            -webkit-text-stroke: 2px $color_dark_orange; } } }
