.home {
    padding-bottom: 0;
    text-align: center;

    .wrapper {
        padding-top: 40px;
        min-height: 100%;
        position: relative;
        background:url(../images/bg-home.jpg) center center fixed {}
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;
        z-index: 9; }

    .title {
        margin-bottom: 25px;
        @include font-size(52px);
        font-family: "SSF4 ABUKET", Open Sans, sans-serif;
        text-transform: uppercase;
        -webkit-text-stroke: 2px $color_dark_orange;

        @include transition(opacity $fade-speed ease-in);

        @include breakpoint(laptop) {
            @include font-size(46px); }

        @include breakpoint(phablet) {
            @include font-size(36px); }

        &.invisible {
            opacity: 0; } }

    .about {
        @include breakpoint(laptop) {
            margin-top: 40px; }

        @include breakpoint(phablet) {
            margin-top: 0; }

        @include transition(opacity $fade-speed ease-in);

        &.invisible {
            opacity: 0; }

        > .text {
            @include font-size(18px);
            line-height: 22px;

            @include breakpoint(laptop) {
                width: 80%;
                margin-left: 10%; }

            @include breakpoint(phablet) {
                width: 95%;
                margin-left: 0; } } } }

